interface chunkType {
    (oldArray: Array<any>, length: number): Array<any>;
    [propName: string]: any;
}

let chunk: chunkType = (data, lengthDdata) => {
    const name: Array<any> = [];
    for (let i: number; i <= data.length / lengthDdata; i++) {
        let center: Array<any> = [];
        for (let k: number = 1; k <= lengthDdata; k++) {
            center.push(data[k]);
        };
        name.push(center);
    }
    return name;
}
console.log(chunk([1,2,3,4,5,6,7,8], 4));
